.game-1i1 {
  padding-top: 40px;
  text-align: center;
  background: #d6d6d6;
  min-height: 100vh;

  & > .box {
    display: inline-block;
    background: white;
    padding: 15px;
    border-radius: 5px;
    border: 1px solid #d6d6d6;
    box-shadow: 2px 2px 4px rgba(black, .1);

    & > .line1 {
      //display: flex;
      //justify-content: space-around;
      white-space: nowrap;

      & > .wordList {
        display: inline-block;
        vertical-align: top;
        //flex: 0 0 auto;
        margin-right: 40px;

        & > .word {
          padding: 5px 15px;
          font-weight: bold;
          color: #9b5e27;

          &.selected {
            //text-decoration: line-through;
            background: #d8fad8;
            color: #037203;
          }

          &.disabled {
            color: #d4d4d4;
          }
        }
      }
      & > .grid {
        //flex: 0 0 auto;
        display: inline-block;
      }
    }

    & > .status {
      & > .congratulations {
        font-size: 30px;
        font-weight: 600;
        padding: 30px 0;
        color: #037203;
      }
    }

    .controls {
      margin-top: 40px;
      text-align: right;
    }
  }
}