.grid-3ow {
  position: relative;

  & > table {
    border-collapse: collapse;
    user-select: none;
    > tbody > tr > td {
      width : 40px;
      height: 40px;
      text-align: center;
      background: #fff7f7;
      border: 1px solid #e5cccc;
      user-select: none;
      font-size: 18px;

      &.green {
        background: #c7ffc7;
      }
    }
  }

  & > .markedLocations {
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    & > .location {
      $size: 30px;
      position: absolute;
      border-radius: $size / 2;

      border: 2px solid #6df16d;

      &.horizontal {
        height: $size;
      }
      &.vertical {
        width: $size;
      }
    }
  }
}
