html, body {
  height: 100%;
  margin: 0;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 14px;
  //color: black;
  font-weight: 400;
  letter-spacing: 0.7px;
  //color: whitesmoke;
  //background: #1d252a;

  a {
    color: #04178c;

    &:visited {
      color: #404462;
